<template>
  <div
    v-loading="loading"
    class="hg-search-view">
    <hg-map
      v-if="Object.keys(options).length && items && items.length"
      :data="items"
      :options="options" />
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import { ElMessage } from 'element-plus'
import HgMap from '@/components/HgMap.vue'
import { ISearch } from '@/typing/interfaces/ISearch'
import { IPlace } from '@/typing/interfaces/IPlace'
import { Api } from '@/utils/Api'

export default defineComponent({
  name: 'MapView',
  components: {
    HgMap
  },
  query: ['search'],
  props: {
    data: {
      type: String,
      default: () => (''),
      required: false
    }
  },
  data() {
    return {
      loading: true,
      search: {} as ISearch,
      items: [] as IPlace[]
    }
  },
  computed: {
    options() {
      if (!this.search?.location) return {}
      try {
        const [lat, lng] = this.search.location.split(',') as string[]
        return {
          center: {
            lat: parseFloat(lat),
            lng: parseFloat(lng)
          },
          zoom: 13
        }
      } catch (error) {
        return {}
      }
    }
  },
  async mounted() {
    try {
      const searchString = JSON.parse(decodeURIComponent(this.$route.query.search as string))
      if (searchString && Object.keys(searchString).length) {
        this.search = searchString as ISearch
        if (this.$route.params?.data) {
          this.items = Object.assign([], JSON.parse(this.$route.params.data as string) as IPlace[])
        } else {
          const { data } = await Api.find(this.search as ISearch)
          if (data?.length) {
            this.items = data
          } else {
            this.backToSearch('No results were found for this query. Please change your search parameters.')
          }
        }
        this.loading = false
      } else {
        this.backToSearch()
      }
    } catch (error) {
      this.backToSearch()
    }
  },
  methods: {
    backToSearch(message = 'Bad request.') {
      this.$router.push({
        name: 'SearchView'
      })
      ElMessage({
        type: 'warning',
        message
      })
    }
  }
})
</script>

