
import { defineComponent } from 'vue'
import { mapLoader } from '@/utils/GoogleMaps'
import { IPlace } from '@/typing/interfaces/IPlace'

export default defineComponent({
  name: 'HgMap',

  props: {
    data: {
      type: Array,
      default: () => ([])
    },
    options: {
      type: Object,
      required: true,
      default: () => ({})
    }
  },
  data() {
    return {
      map: null,
      opened: [] as unknown[]
    }
  },
  mounted() {
    mapLoader
      .load()
      .then(() => {
        if (typeof (window as any).google?.maps?.Map !== 'function') return
        this.map = new (window as any).google.maps.Map(this.$refs.mapElement, this.options)
        // eslint-disable-next-line @typescript-eslint/camelcase
        const items = (this.data as IPlace[])?.map(({ place_name, location }) => {
          return [
            // eslint-disable-next-line @typescript-eslint/camelcase
            place_name,
            location[1],
            location[0],
            1
          ]
        })
        // eslint-disable-next-line no-new
        new (window as any).google.maps.Marker({
          position: this.options.center,
          animation: (window as any).google.maps.Animation.DROP,
          map: this.map,
          title: 'Current position'
        })
        this.setMarkers(items)
      })
      .catch(e => {
        // do something
        console.log('catch', e)
      })
    console.log(this.data)
  },
  methods: {
    setMarkers(items: any) {
      if (!(window as any)?.google?.maps || !this.map) return
      console.log('a', `${window.location.protocol}//${window.location.host}/map-icon_50.png`)
      // Adds markers to the map.

      // Marker sizes are expressed as a Size of X,Y where the origin of the image
      // (0,0) is located in the top left of the image.

      // Origins, anchor positions and coordinates of the marker increase in the X
      // direction to the right and in the Y direction down.
      const image = {
        url: `${window.location.protocol}//${window.location.host}/map-icon_50.png`,
        // This marker is 50 pixels wide by 50 pixels high.
        size: new (window as any).google.maps.Size(50, 50),
        // The origin for this image is (0, 0).
        origin: new (window as any).google.maps.Point(0, 0),
        // The anchor for this image is the base of the flagpole at (0, 50).
        anchor: new (window as any).google.maps.Point(-25, 0)
      }
      // Shapes define the clickable region of the icon. The type defines an HTML
      // <area> element 'poly' which traces out a polygon as a series of X,Y points.
      // The final coordinate closes the poly by connecting to the first coordinate.
      const shape = {
        coords: [0, 0, 50, 50],
        type: 'rect'
      }

      for (let i = 0; i < items.length; i++) {
        const marker = items[i]

        const infowindow = new (window as any).google.maps.InfoWindow({
          content: `
          <div id="content">
            <a href="//www.google.com/search?q=${marker[0]}" target="_blank">${marker[0]}</a>
          </div>`
        })

        // eslint-disable-next-line no-new
        const markerInstance = new (window as any).google.maps.Marker({
          position: { lat: marker[1], lng: marker[2] },
          map: this.map,
          icon: image,
          shape: shape,
          animation: (window as any).google.maps.Animation.DROP,
          title: marker[0],
          zIndex: marker[3]
        })
        markerInstance.addListener('click', () => {
          this.$nextTick(() => {
            if (this.opened.indexOf(markerInstance) >= 0) {
              infowindow.close()
              return
            } else {
              this.opened.every(item => (item as any).close())
              this.opened = []
            }
            infowindow.open(this.map, markerInstance)
            this.opened.push(infowindow)
          })
        })
      }
    }
  }
})
